ara aquellas especies con bajo requerimiento hídrico, se debe focalizar el riego sobre los ejemplares con mayor necesidad, pues excesos de agua pueden ocasionar la aparición de hongos, así como pudrición de raíces.")])])]),_c('div',{staticClass:"col-lg-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/tema2-img-14.svg")}})])])])])],1),_c('h3',{staticClass:"mt-4"},[_vm._v("Gestión de residuos en el endurecimiento ")]),_c('p',{staticClass:"mt-4"},[_vm._v("Para finalizar este tema, vamos a asimilar lo relacionado con la gestión de residuos en el endurecimiento.")]),_c('div',{staticClass:"row justify-content-around align-items-center mt-5"},[_c('div',{staticClass:"col-lg-7"},[_c('AcordionA',{attrs:{"tipo":"b","clase-tarjeta":"tarjeta tarjeta--azul"}},[_c('div',{staticClass:"row",attrs:{"titulo":"Protocolos en el manejo de residuos"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("En términos de establecimiento del laboratorio y de la práctica asociada con la micropropagación de material vegetal "),_c('i',[_vm._v("in vitro")]),_vm._v(", se establecen inicialmente protocolos en lo relacionado con el manejo de residuos que, si bien incluyen la totalidad del proceso, es necesario realizar una diferenciación de la fase específica de la aclimatación, debido a que se trata de uno de los momentos más álgidos de la totalidad del ejercicio, pues implica la realización de algunas actividades donde se encuentra en vulnerabilidad manifiesta, el frágil material obtenido frente a las amenazas siempre latentes en contextos productivos de este tipo y que pueden comprometer el logro del objetivo, a las puertas de su consecución.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Tipos de residuos"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Entre los residuos generados en esta fase, se resaltan los asociados con los medios de cultivo que permitieron el desarrollo inicial de las plántulas. Este tipo de compuestos, ric